<template>
    <div class="loading-cover">
        <h1>
            {{fixedPercent}}
        </h1>
    </div>
</template>

<script>
    import {gsap,Power2} from 'gsap'
    import $ from 'jquery'
    export default {
        name: "LoadingCover",
        props:['percent'],
        data(){
            return{
                tweenPercent:0,
            }
        },
        computed:{
            fixedPercent:function () {
                return this.tweenPercent.toFixed(0);
            }
        },
        watch:{
            percent:function(newValue) {
                gsap.to(this.$data, {
                    duration: 0.8,
                    tweenPercent: newValue,
                    ease:Power2.easeInOut,
                });
            }
        },
        methods:{
            hide(){
                gsap.to('.loading-cover',{
                    duration:0.8,
                    left:-window.innerWidth,
                    ease:Power2.easeInOut,
                    onComplete:()=>{
                        this.$emit('loadingCoverHided');
                        $('.loading-cover').hide();
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .loading-cover{
        width: 100vw;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        font-size: 4rem;
        color: var(--gray3);
        background: var(--gray1);
        z-index: 99;
        overflow: hidden;
    }
    h1{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        font-size: 16rem;
        color: var(--gray3);
        -webkit-text-stroke:1px var(--gray3);
        -webkit-text-fill-color:transparent;
    }
</style>