<template>
  <div id="app">
    <LoadingCover ref="loadingcover" :percent="totalLoadingPercent" ></LoadingCover>
    <MyCursor></MyCursor>
    <Header ref="header" @toggleTitle="toggleTitle" @updateLoadingPercent = "updateLogoLoadingPercent"></Header>
    <Showcase ref="showcase" @loadingCoverHided="loadingCoverHided" @updateLoadingPercent = "updateShowcaseLoadingPercent"></Showcase>
  </div>
</template>

<script>
  import MyCursor from "@/components/MyCursor";
  import Header from "@/components/Header";
  import Showcase from "@/components/Showcase";
  import LoadingCover from '@/components/LoadingCover'

  export default {
    name: 'App',

    components: {
      Showcase,
      MyCursor,
      Header,
      LoadingCover
    },
    data(){
      return{
        showcaseLoadingPercent:0,
        logoLoadingPercent:0,
        isTitleHide: true,
      }
    },
    computed:{
      totalLoadingPercent:function(){
        return (this.showcaseLoadingPercent + this.logoLoadingPercent) / 2
      }
    },
    methods:{
      loadingCoverHided(){
        this.$refs.loadingcover.hide();
        setTimeout(()=>{
          this.$refs.header.showViewWorks();
        },800)
      },
      updateShowcaseLoadingPercent(newValue){
        this.showcaseLoadingPercent = newValue
      },
      updateLogoLoadingPercent(newValue){
        this.logoLoadingPercent = newValue
      },
      toggleTitle(){
        this.isTitleHide ? this.$refs.showcase.showTitle():this.$refs.showcase.hideTitle();
        this.isTitleHide = !this.isTitleHide;
      }
    },
  }
</script>

<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    position: relative;
    z-index: 1;
    overflow: hidden;
  }

</style>
