<template>
    <div class="detail-wrapper">
        <div class="detail-container" id="detail-container">
            <div class="intro">
                <h1 class="percent">{{fixedPercent}}</h1>
                <h1 class="title">{{title}}</h1>
            </div>
            <div class="images">
            </div>
        </div>
    </div>

</template>

<script>
    import ProjectData from '../project-data.js';
    import Scrollbar from 'smooth-scrollbar';
    import MobilePlugin from "@/utils/mobile-plugin"
    import {gsap,Power2} from 'gsap'
    import $ from 'jquery'

    export default {
        name: "DetailPage",
        data(){
            return{
                scrollbar : null,
                images :[],
                loadingPercent:0,
                tweenPercent:0,
                preloadIndex:0,
            }
        },
        mounted(){
            this.scrollbar = Scrollbar.init(document.querySelector('#detail-container'),{
                damping:0.05,
                continuousScrolling : false,
                plugins:{
                    mobile:{
                        speed:0.2
                    }
                }
            });
            Scrollbar.use(MobilePlugin);
        },
        computed:{
            projects:function(){
                return ProjectData.data().projectData.reverse().filter(function (item) {
                    return item.isShowcases;
                });
            },
            detailsImages: function () {
                return this.projects[this.preloadIndex].detailsPage
            },
            fixedPercent:function () {
                return this.tweenPercent.toFixed(0);
            },
            title:function () {
                return this.projects[this.preloadIndex].title
            },
            isMobile : function () {
                return window.matchMedia('(max-width: 767px)').matches
            }
        },
        watch:{
            loadingPercent:function(newValue) {
                gsap.to(this.$data, {
                    duration: 0.8,
                    tweenPercent: newValue,
                    onComplete:()=>{
                        if( this.fixedPercent == 100){
                            gsap.to($('.percent'),{
                                delay:0.2,
                                duration:0.4,
                                opacity:0,
                            });
                            gsap.to($('.title'),{
                                delay:0.6,
                                duration:0.8,
                                opacity:1,
                            })
                        }
                    }
                });
            }
        },
        methods:{
            open(i){
                gsap.to($('.percent'),{
                    duration:0.8,
                    opacity:1,
                    ease:Power2.easeInOut
                });
                this.loadImage(i);
            },
            close(){
                this.images = [];
                this.loadingPercent = 0;
                this.scrollToTop();
                gsap.to($('.percent'),{
                    duration:0,
                    opacity:1,
                });
                gsap.to($('.title'),{
                    duration:0.8,
                    opacity:0,
                });
                gsap.to($('.intro'),{
                    delay:0.8,
                    duration:0.8,
                    height: '100vh',
                    ease:Power2.easeInOut,
                    onComplete:()=>{
                        $('.images').empty();
                    }
                });
            },
            scrollToTop(){
                this.scrollbar.setMomentum(0,0);
                this.scrollbar.scrollTo(0,0);
            },

            loadImage(i){
                this.preloadIndex = i;
                this.preload(this.detailsImages, ()=> {
                        gsap.to($('.intro'),{
                            delay:1.4,
                            duration:0.8,
                            height:'95vh',
                            ease:Power2.easeInOut,
                            onStart:()=>{
                                this.$emit('allImagesLoaded');
                            }
                        });

                });
            },
            preload(images, allImagesLoadedCallback) {
                let loadedCounter = 0;
                const toBeLoadedNumber = images.length;
                const preloadImage = (i,t, anImageLoadedCallback) => {
                    const image = new Image();
                    image.src = t;
                    image.onload =()=> {
                        this.images[i] = image;
                        anImageLoadedCallback();
                    }

                };
                for(let i = 0; i< toBeLoadedNumber;i++){
                    preloadImage(i,images[i], () => {
                        loadedCounter += 1;
                        this.loadingPercent =loadedCounter / toBeLoadedNumber * 100;
                        if (loadedCounter === toBeLoadedNumber) {
                            this.images.forEach((image)=>{
                                $('.images').append(image);
                            });
                            allImagesLoadedCallback()
                        }
                    })
                }
            },
        },
        props:["projectIndex"],

    }
</script>

<style scoped>
    .detail-wrapper{
        width: 100vw;
        height: 100%;
        left: 0;
        bottom: 0;
        position: absolute;
        z-index: 2;
        box-sizing: border-box;
        display: none;
    }
    .detail-container{
        width: 100vw;
        height:0;
        left: 0;
        bottom: 0;
        position: absolute;
        z-index: 2;
        box-sizing: border-box;
    }
    .intro{
        width: 100%;
        height: 100vh;
        position: relative;
    }
    .intro .percent{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        font-size: 16rem;
        color: var(--white);
        -webkit-text-stroke:1px var(--white);
        -webkit-text-fill-color:transparent;
        opacity: 0;
    }
    .intro .title {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        font-size: 4rem;
        color: var(--white);
        opacity: 0;
    }
    .images{
        background: var(--background-color);
        width: 100vw;
        font-size: 0;
        box-sizing: border-box;
    }
</style>