<template>
    <div class="header">
        <div class="logo">
            <div class="switchImg">
            </div>
            <img src="../assets/brand/logo.svg" alt="">
        </div>
        <canvas id="logo-canvas" ></canvas>
        <h3 class="clickToViewWorks interactive-s" @click="toggle">view my works</h3>
    </div>

</template>

<script>
    import {gsap,Power2} from 'gsap'
    import $ from 'jquery'
    import * as THREE from 'three'
    import {getRatio} from "@/utils/utils";
    import vertexShader from '../glsl/vertexShader.glsl'
    import fragmentShader from '../glsl/waveShader.glsl'

    export default {
        name: "Header",
        data(){
            return{
                camera: null,
                scene: null,
                renderer: null,
                canvas:null,
                geometry:null,
                clock:null,
                mesh:null,
                perspective : 800,
                loader: new THREE.TextureLoader(),
                u_time:0,

                sizes: new THREE.Vector2(0,0),
                offset: new THREE.Vector2(0,0),
                mouse: new THREE.Vector2(1000,0),

                isFold : true,
                currentIndex:0,

                imagesData:[],
                preloadImages:[],
                lazyLoadImages:[],
                images:[],
                imagesCount:9,
            }
        },
        mounted(){
            for(let i=0;i<this.imagesCount;i++){
                this.imagesData.push(require('../assets/brand/img=img'+(i+1)+'.png'));
            }
            for(let i=0;i<3;i++){
                this.preloadImages.push(this.imagesData[i]);
            }

            this.preload(this.preloadImages,()=>{
                for(let i=3;i<this.imagesCount;i++){
                    if(this.images[i+3] == null){
                        const image = this.loader.load(this.imagesData[i],()=> {
                            this.images[i] = image;
                            console.log(this.images);
                        });
                    }
                }
                this.init();
            })
        },
        components: {
        },
        methods:{
            init(){
                this.canvas = document.getElementById('logo-canvas');
                this.scene = new THREE.Scene({});
                //Renderer
                this.renderer = new THREE.WebGLRenderer({
                    alpha: true,
                    canvas: this.canvas
                });
                this.renderer.setSize(window.innerWidth,window.innerHeight);
                this.renderer.setPixelRatio(Math.min(window.devicePixelRatio,2));
                this.clock = new THREE.Clock();
                this.geometry = new THREE.PlaneBufferGeometry(1, 1, 16, 16);

                this.setLights();
                this.setCamera();
                this.figure();

                this.update();
                setInterval(this.switchNextImg,6400);
                window.addEventListener('resize',()=>{
                    this.onResize();
                });
                $(document).on('click','.logo',()=>{
                    this.unFold();
                });
            },
            setLights() {
                const ambientlight = new THREE.AmbientLight(0xffffff, 2)
                this.scene.add(ambientlight)
            },
            setCamera(){
                const fov = (180 * (2 * Math.atan(window.innerHeight / 2 / this.perspective))) / Math.PI;
                this.camera = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, 1, 1000);
                this.camera.position.set(0, 0, this.perspective);
                this.camera.updateProjectionMatrix();
            },
            figure(){
                this.getBounds($('.switchImg')[0]);
                this.createMesh(this.images[this.currentIndex],this.images[this.currentIndex+1]);
            },
            getBounds(target){
                const { width, height, left, top } = target.getBoundingClientRect();

                if (!this.sizes.equals(new THREE.Vector2(width, height))) {
                    this.sizes.set(width, height)
                }
                if (!this.offset.equals(new THREE.Vector2(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2))) {
                    this.offset.set(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2)
                }
            },
            createMesh(image,hoverImage) {
                const uniforms = {
                    u_map: { type: 't', value: image },
                    u_hovermap:{ type: 't', value: hoverImage},
                    u_alpha:{value:1},
                    u_ratio:{value: getRatio(this.sizes,image.image)},
                    u_hoverratio:{value: getRatio(this.sizes,image.image)},
                    u_time: { value: this.u_time},
                    u_mouse: { value: this.mouse},
                    u_res: { value: new THREE.Vector2(window.innerWidth, window.innerHeight) },
                    u_progressHover:{value:0},
                    u_progressClick:{value:0},
                };
                const material = new THREE.ShaderMaterial({
                    uniforms: uniforms,
                    vertexShader: vertexShader,
                    fragmentShader: fragmentShader,
                    defines: {
                        PR: window.devicePixelRatio.toFixed(1)
                    },
                });
                this.mesh = new THREE.Mesh(this.geometry, material);
                this.mesh.position.set(this.offset.x, this.offset.y, 0);
                this.mesh.scale.set(this.sizes.x, this.sizes.y, 1);
                this.scene.add(this.mesh);
            },
            toggle(){
                this.isFold? this.fold(): this.unFold()
            },
            switchNextImg(){
                this.mesh.material.uniforms.u_hovermap.value = this.images[this.currentIndex];
                gsap.to(this.mesh.material.uniforms.u_progressHover,{
                    duration:1.6,
                    value:1,
                    ease:Power2.easeInOut,
                    onComplete:()=>{
                        if(this.currentIndex < this.images.length - 1){
                            this.currentIndex +=1;
                        }else{
                            this.currentIndex =0;
                        }
                        this.mesh.material.uniforms.u_map.value = this.images[this.currentIndex];
                        gsap.to(this.mesh.material.uniforms.u_progressHover,{
                            duration:1.6,
                            value:0,
                            ease:Power2.easeInOut,
                            onComplete:()=>{
                                this.mesh.material.uniforms.u_hovermap.value = this.images[this.currentIndex+1];
                            }
                        })
                    }
                });
            },
            fold() {
                gsap.to('.header', {
                    duration:0.8,
                    height: '8rem',
                    ease: Power2.easeInOut
                });
                gsap.to('.logo', {
                    duration:0.8,
                    width: '4rem',
                    ease: Power2.easeInOut,
                    onUpdate:()=>{
                        this.getBounds($('.switchImg')[0]);
                        this.mesh.position.set(this.offset.x, this.offset.y, 0);
                        this.mesh.scale.set(this.sizes.x, this.sizes.y, 1);
                        this.mesh.material.uniforms.u_res.value.set(window.innerWidth, window.innerHeight);
                    }
                });
                gsap.to('.main', {
                    duration:0.8,
                    top:0,
                    ease: Power2.easeInOut,
                });
                gsap.to('.main', {
                    duration:1.2,
                    opacity:1,
                    ease: Power2.easeInOut,
                });

                gsap.to('.clickToViewWorks',{
                    duration:0.8,
                    opacity:0,
                    onComplete:()=>{
                        this.$emit('toggleTitle')
                        $('.clickToViewWorks').hide();
                        $('.clickToViewWorks').removeClass('interactive-s');
                    }
                });
                $('.logo').addClass('interactive-s');
                this.isFold = !this.isFold;
            },
            update(){
                requestAnimationFrame(this.update);
                this.renderer.render(this.scene,this.camera);
                if(this.mesh){
                    this.mesh.material.uniforms.u_time.value += this.clock.getDelta();
                }
            },
            unFold(){
                gsap.to('.header',{
                    duration:0.8,
                    height:'100%',
                    ease:Power2.easeInOut,
                });
                gsap.to('.logo',{
                    duration:0.8,
                    width:'64vw',
                    ease:Power2.easeInOut,
                    onUpdate:()=>{
                        this.getBounds($('.switchImg')[0]);
                        this.mesh.position.set(this.offset.x, this.offset.y, 0);
                        this.mesh.scale.set(this.sizes.x, this.sizes.y, 1);
                        this.mesh.material.uniforms.u_res.value.set(window.innerWidth, window.innerHeight);
                    }
                });
                gsap.to('.main', {
                    duration:0.8,
                    top:'100%',
                    ease: Power2.easeInOut,
                });
                gsap.to('.main',{
                    duration:0.4,
                    opacity:0,
                    ease: Power2.easeInOut,
                });
                $('.clickToViewWorks').show();
                gsap.to('.clickToViewWorks',{
                    delay:0.4,
                    duration:0.8,
                    opacity:1,
                    onComplete:()=>{
                        $('.clickToViewWorks').addClass('interactive-s');
                    }
                });
                $('.logo').removeClass('interactive-s');
                this.$emit('toggleTitle');
                this.isFold = !this.isFold;
            },
            showViewWorks(){
                gsap.fromTo('.clickToViewWorks',{
                    y:-32,
                    opacity:0,
                },{
                    y:0,
                    duration:0.8,
                    opacity:1,
                });
            },
            preload(imgs, allImagesLoadedCallback) {
                let loadedCounter = 0;
                const toBeLoadedNumber = imgs.length;
                const preloadImage = (t, anImageLoadedCallback) => {
                    const image = this.loader.load(t, anImageLoadedCallback);
                    this.images.push(image);
                };
                imgs.forEach((img) => {
                    preloadImage(img, () => {
                        loadedCounter += 1;
                        this.loadingPercent = loadedCounter / toBeLoadedNumber * 100;
                        this.$emit('updateLoadingPercent',this.loadingPercent);
                        if (loadedCounter === toBeLoadedNumber) {
                            allImagesLoadedCallback()
                        }
                    })
                })
            },
            onResize() {
                this.setCamera();
                this.renderer.setSize(window.innerWidth,window.innerHeight);
                    this.getBounds($('.switchImg')[0]);
                    this.mesh.scale.set(this.sizes.x, this.sizes.y, 1);
                    this.mesh.material.uniforms.u_res.value.set(window.innerWidth, window.innerHeight);
                    this.mesh.material.uniforms.u_ratio.value = getRatio(this.sizes,this.images[this.currentIndex].image);
                    this.mesh.material.uniforms.u_hoverratio.value = getRatio(this.sizes,this.images[this.currentIndex].image);
            },
        }
    }
</script>

<style scoped>
    .header {
        position: absolute;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        z-index: 2;
    }
    .logo{
        position: absolute;
        width: 64vw;
        height: auto;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    img{
        width: 100%;
    }
    .navigation{
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        margin-right: 2rem;
    }
    .navigation a{
        display: inline-block;
        height: auto;
        width: auto;
        margin-left: 2rem;
        box-sizing: border-box;
        transition: 0.5s;
        font-size: 1rem;

    }
    .switchImg{
        position: absolute;
        width: 90%;
        height: 55%;
        right: 0;
        top: calc(20% - 2px);
        z-index: -1;
    }
    .clickToViewWorks{
        position: absolute;
        bottom: 4rem;
        left: 50%;
        transform: translateX(-50%);
        color: var(--white);
        padding: 1rem;
        opacity: 0;
    }
    #logo-canvas{
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100%;
        z-index: 1;
        pointer-events: none;
    }
    button{
        width: 4rem;
        height: 4rem;
        position: absolute;
        top: 4rem;
        left: 4rem;
    }
</style>