<template>
    <div class="main">
        <div class="back-button interactive-s"></div>
        <ScrollBar :scroll-y="scrollY" ref="scrollbar"></ScrollBar>
        <DetailPage :projectIndex ="clickTargetIndex" ref="detailpage" @allImagesLoaded = "detailImagesLoaded"> </DetailPage>
        <div class="showcase-container" id="smooth-scroll">
            <div class="showcase-figure" v-for="item in projects" :key = "item.id">
                <div
                    :data-src="item.dataSrc"
                    :data-hover="item.dataHover"
                    class="showcase-image"
                    alt="My image"
                >
                </div>
            </div>
        </div>
        <div class="title-container">
            <h1 class="title-content" v-for="item in projects" :key = "item.id">{{item.title}}</h1>
        </div>
        <canvas id="showcase-canvas"></canvas>
    </div>
</template>

<script>
    import * as THREE from 'three'
    import {gsap,Power2} from 'gsap'
    import $ from 'jquery';
    import vertexShader from '../glsl/vertexShader.glsl'
    import fragmentShader from '../glsl/trippyShader.glsl'
    import ProjectData from '../project-data.js'
    import Scrollbar from 'smooth-scrollbar'
    import DetailPage from '@/components/DetailPage'
    import {getRatio,map} from '@/utils/utils'
    import ScrollBar from '@/components/ScrollBar'
    import ModalPlugin from '@/utils/modal'
    import MobilePlugin from "@/utils/mobile-plugin"


    export default {
        name: "Showcase",
        data() {
            return {
                camera: null,
                scene: null,
                renderer: null,
                canvas:null,
                geometry:null,
                clock:null,
                meshes:[],
                images:[],
                perspective : 800,
                loader: new THREE.TextureLoader(),
                u_time:0,

                u_offset:new THREE.Vector2(0,0),
                sizes: new THREE.Vector2(0,0),
                offset: new THREE.Vector2(0,0),
                mouse: new THREE.Vector2(0,0),
                delta: new THREE.Vector2(0,0),

                isHovering:false,
                isOpenDetailPage:false,
                hoveringTargetIndex:0,
                clickTargetIndex:0,
                inViewportTargetIndex:0,

                loadingPercent:0,

                scrollbar: null,
                scrollY :0,
            }
        },
        components:{
            DetailPage,
            ScrollBar,
        },
        mounted(){
            for (let i=0;i< this.projects.length;i++){
                this.getBounds($('.showcase-image')[i]);
                gsap.to($('.title-content')[i],{
                    x: this.offset.x -this.delta.x,
                    y: this.offset.y + this.delta.y,
                });
            }
            this.preload(this.projects,()=> {
                setTimeout(()=> {
                    this.init();
                },800);
            });

        },
        computed:{
            projects: function () {
                return ProjectData.data().projectData.reverse().filter(function (item) {
                    return item.isShowcases;
                })
            },
            isMobile : function () {
                return window.matchMedia('(max-width: 767px)').matches
            }
        },
        methods:{
            /*Action
            --------------------------------*/
            init(){
                this.scrollbar = Scrollbar.init(document.querySelector('#smooth-scroll'),{
                    damping:0.05,
                    continuousScrolling : false,
                    plugins:{
                        mobile:{
                            speed:0.2
                        }
                    }
                });
                Scrollbar.use(ModalPlugin);
                Scrollbar.use(MobilePlugin);

                this.canvas = document.getElementById('showcase-canvas');
                this.scene = new THREE.Scene({});
                //Renderer
                this.renderer = new THREE.WebGLRenderer({
                    alpha: true,
                    canvas: this.canvas
                });
                this.renderer.setSize(window.innerWidth,window.innerHeight);
                this.renderer.setPixelRatio(Math.min(window.devicePixelRatio,2));
                this.clock = new THREE.Clock();
                this.geometry = new THREE.PlaneBufferGeometry(1, 1, 16, 16);

                this.setLights();
                this.setCamera();

                this.figure();

                this.update();

                this.bindListener();
                this.$emit('loadingCoverHided');
                this.hideTitle();
            },
            setLights(){
                const ambientlight = new THREE.AmbientLight(0xffffff, 2)
                this.scene.add(ambientlight)
            },
            setCamera(){
                const fov = (180 * (2 * Math.atan(window.innerHeight / 2 / this.perspective))) / Math.PI;
                this.camera = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, 1, 1000);
                this.camera.position.set(0, 0, this.perspective);
                this.camera.updateProjectionMatrix();
            },
            figure(){
                for (let i=0;i< this.projects.length;i++){
                    this.getBounds($('.showcase-image')[i]);
                    this.createMesh(this.images[i]);
                }
            },
            getBounds(target){
                const { width, height, left, top } = target.getBoundingClientRect();
                if (!this.sizes.equals(new THREE.Vector2(width, height))) {
                    this.sizes.set(width, height)
                }
                if (!this.offset.equals(new THREE.Vector2(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2))) {
                    this.offset.set(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2)
                }
            },
            createMesh(image) {
                const uniforms = {
                    u_map: { type: 't', value: image },
                    u_alpha:{value:1},
                    u_ratio:{value: getRatio(this.sizes,image.image)},
                    u_hoverratio:{value: getRatio(this.sizes,image.image)},
                    u_mouse: { value: this.mouse},
                    u_time: { value: this.u_time},
                    u_res: { value: new THREE.Vector2(window.innerWidth, window.innerHeight) },
                    u_offset:{value: this.u_offset},
                    u_progressHover:{value:0},
                    u_progressClick:{value:0},
                };
                const material = new THREE.ShaderMaterial({
                    uniforms: uniforms,
                    vertexShader: vertexShader,
                    fragmentShader: fragmentShader,
                    defines: {
                        PR: window.devicePixelRatio.toFixed(1)
                    },

                });
                const mesh = new THREE.Mesh(this.geometry, material);
                mesh.position.set(this.offset.x, this.offset.y, 0);
                mesh.scale.set(this.sizes.x, this.sizes.y, 1);
                this.meshes.push(mesh);
                this.scene.add(mesh);
            },
            openDetailPage(e){
                this.hideTitle();
                $('.detail-wrapper').show();
                this.isOpenDetailPage = true;
                this.clickTargetIndex = $(e.target).parent().index();
                this.scrollbar.updatePluginOptions('modal',{ open: true });
                this.scrollbar.setMomentum(0,0);
                this.scrollbar.scrollIntoView($('.showcase-figure')[this.clickTargetIndex]);

                const newBound = {
                    x:window.innerWidth * 1.05,
                    y:window.innerHeight *1.05
                };
                const newRatio = getRatio(newBound,this.images[this.clickTargetIndex].image);
                gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_hoverratio.value,0.8,{
                    x:newRatio.x,
                    y:newRatio.y,
                    ease:Power2.easeInOut
                });
                gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_ratio.value,0.8,{
                    x:newRatio.x,
                    y:newRatio.y,
                    ease:Power2.easeInOut
                });

                gsap.to($('.showcase-image')[this.clickTargetIndex],0,{
                    width:105+'%',
                    height:105+'%',
                    left:0,
                    top:0,
                    onComplete:()=>{
                        gsap.to($('.detail-container'),{
                            duration:0.8,
                            height:100 + 'vh',
                        });
                        this.getBounds($('.showcase-image')[this.clickTargetIndex]);
                        gsap.to(this.meshes[this.clickTargetIndex].scale,0.8,{
                            x:this.sizes.x,
                            y:this.sizes.y
                        });
                        gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_res.value,0.8,{
                            x:window.innerWidth,
                            y:window.innerHeight
                        });
                        gsap.fromTo($('.back-button'),{
                            scale:0,
                            opacity:0,
                        },{
                            duration:0.8,
                            scale:1,
                            opacity:1,
                            ease:Power2.easeInOut
                        });
                    }
                });
                gsap.to(this.u_offset, 0.8, {
                    x: 0,
                    y: 0
                });
                this.$refs.detailpage.open(this.clickTargetIndex);
            },
            closeDetailPage(){
                this.scrollbar.updatePluginOptions('modal',{ open: false });
                gsap.to($('.back-button'),0.8,{
                    scale:0,
                    ease:Power2.easeInOut,
                    opacity:0
                });
                gsap.to($('.detail-container'),{
                    duration:0.8,
                    height:0,
                    onComplete:()=>{
                        this.$refs.detailpage.close();
                        const newBound = {
                            x: parseFloat(window.getComputedStyle(document. documentElement)["fontSize"])*24,
                            y:parseFloat(window.getComputedStyle(document. documentElement)["fontSize"])*24,
                        };
                        const newRatio = getRatio(newBound,this.images[this.clickTargetIndex].image);
                        gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_hoverratio.value,0.8,{
                            x:newRatio.x,
                            y:newRatio.y,
                            ease:Power2.easeInOut
                        });
                        gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_ratio.value,0.8,{
                            x:newRatio.x,
                            y:newRatio.y,
                            ease:Power2.easeInOut
                        });
                        gsap.to($('.showcase-image')[this.clickTargetIndex],{
                            duration:0.8,
                            width:'24rem',
                            height:'24rem',
                            left:'calc(50% - 12rem)',
                            top:'calc(50% - 12rem)',
                            ease:Power2.easeInOut,
                            onUpdate:()=>{
                                this.getBounds($('.showcase-image')[this.clickTargetIndex]);
                                this.meshes[this.clickTargetIndex].scale.set(this.sizes.x, this.sizes.y, 1);
                                this.meshes[this.clickTargetIndex].material.uniforms.u_res.value.set(window.innerWidth, window.innerHeight);
                            },
                            onComplete:()=>{
                                gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_progressClick,{
                                    duration:0.8,
                                    value: 0,
                                    ease:Power2.easeInOut
                                });
                                gsap.to(this.meshes[this.hoveringTargetIndex].material.uniforms.u_progressHover,{
                                    duration:0.5,
                                    value: 0
                                });

                                this.isHovering = false;
                                this.isOpenDetailPage = false;
                                $('.detail-wrapper').hide();
                                this.showTitle();
                            },
                        });
                    }
                });
            },
            showTitle(){
                gsap.fromTo('.title-content',{
                    x:128,
                    opacity:0,
                },{
                    duration:0.8,
                    x:0,
                    opacity:1,
                    ease:Power2.easeInOut,
                });
                this.$refs.scrollbar.show();
            },
            hideTitle(){
                gsap.to('.title-content',0.8,{
                    opacity:0,
                    ease:Power2.easeInOut
                });
                this.$refs.scrollbar.hide();
            },
            update(){
                requestAnimationFrame(this.update);
                this.renderer.render(this.scene,this.camera);
                if(this.isHovering) {
                    this.meshes[this.hoveringTargetIndex].material.uniforms.u_time.value += this.clock.getDelta();
                }
                if(!this.isOpenDetailPage){
                    for (let i=0;i< this.projects.length;i++){
                        if(!(this.meshes[i].position.x === this.offset.x + this.delta.x
                            &&this.meshes[i].position.y === this.offset.y + this.delta.y)
                        ){
                            this.getBounds($('.showcase-image')[i]);
                            gsap.to(this.meshes[i].position,{
                                x:this.offset.x + this.delta.x,
                                y:this.offset.y + this.delta.y
                            });
                            gsap.to($('.title-content')[i],0.4,{
                                x: this.offset.x -this.delta.x,
                                y: -this.offset.y + this.delta.y,
                                ease:Power2.easeOut
                            });
                        }

                    }
                }
                else {
                    for (let i=0;i< this.projects.length;i++){
                        if(!(this.meshes[i].position.x === this.offset.x + this.delta.x
                            &&this.meshes[i].position.y === this.offset.y + this.delta.y)
                        ){
                            this.getBounds($('.showcase-image')[i]);
                            gsap.to(this.meshes[i].position,{
                                x:this.offset.x,
                                y:this.offset.y
                            });
                        }
                    }
                }
            },
            preload(projects, allImagesLoadedCallback) {

                let loadedCounter = 0;
                const toBeLoadedNumber = projects.length;
                const preloadImage = (t, anImageLoadedCallback) => {
                    const image = this.loader.load(t, anImageLoadedCallback);
                    this.images.push(image);
                };
                projects.forEach((project) => {
                    preloadImage(project.dataSrc, () => {
                        loadedCounter += 1;
                        this.loadingPercent = loadedCounter / toBeLoadedNumber * 100;
                        this.$emit('updateLoadingPercent',this.loadingPercent);
                        if (loadedCounter === toBeLoadedNumber) {
                            allImagesLoadedCallback()
                        }
                    })
                })
            },
            /*Event
            --------------------------------*/
            bindListener(){
                window.addEventListener('mousemove', (ev) => {
                    this.onMouseMove(ev)
                });
                window.addEventListener('resize',()=>{
                    this.onResize();
                });
                window.addEventListener('scroll',()=>{
                    this.onScroll();
                });
                this.scrollbar.addListener((s)=>{
                    this.onScroll(s);
                });
                $(document).on('mouseover','.showcase-image',(e)=>{
                    this.isHovering = true;
                    this.hoveringTargetIndex = $(e.target).parent().index();
                    gsap.to(this.meshes[this.hoveringTargetIndex].material.uniforms.u_progressHover,{
                        duration:0.5,
                        value: 1
                    });
                }).on('mouseout','.showcase-image',()=>{
                    if(this.isOpenDetailPage){
                        return
                    }
                    this.isHovering = false;
                    gsap.to(this.meshes[this.hoveringTargetIndex].material.uniforms.u_progressHover,{
                        duration:0.5,
                        value: 0
                    });
                });
                $(document).on('click','.showcase-image',(e)=>{
                    this.openDetailPage(e);
                });
                $(document).on('click','.back-button',()=>{
                    this.closeDetailPage();
                })
            },
            onResize() {
                this.setCamera();
                this.renderer.setSize(window.innerWidth,window.innerHeight);
                for(let i=0;i< this.projects.length;i++){
                    this.getBounds($('.showcase-image')[i]);
                    this.meshes[i].scale.set(this.sizes.x, this.sizes.y, 1);
                    this.meshes[i].material.uniforms.u_res.value.set(window.innerWidth, window.innerHeight);
                    this.meshes[i].material.uniforms.u_ratio.value = getRatio(this.sizes,this.images[i].image);
                    this.meshes[i].material.uniforms.u_hoverratio.value = getRatio(this.sizes,this.images[i].image);
                }
            },
            onMouseMove(event) {
                if(this.isMobile) return;
                const tX = event.clientX;
                const tY = event.clientY;
                gsap.to(this.mouse, 0.8, {
                    x: tX,
                    y: tY,
                    onUpdate: () =>{
                        if(!this.isOpenDetailPage){
                            this.u_offset.x = (tX - this.mouse.x) * 0.0001;
                            this.u_offset.y = -(tY - this.mouse.y)  * 0.0001;
                            this.delta.x = map(this.mouse.x,0,window.innerWidth,-1,1) * 16;
                            this.delta.y = map(this.mouse.y,0,window.innerHeight,1,-1) * 16;
                        }
                    },
                });

            },
            onScroll({ limit, offset }){
                this.scrollY = offset.y / limit.y;
            },
            detailImagesLoaded(){
                gsap.to(this.meshes[this.clickTargetIndex].material.uniforms.u_progressClick,{
                    duration:0.8,
                    value: 1,
                    ease:Power2.easeInOut,
                });
            },
        }
    }
</script>

<style scoped>
    .main {
        width: 100vw;
        height: 100%;
        position: relative;
        top: 100vh;
    }
    .showcase-container {
        align-items: center;
        justify-content: center;
        z-index: 1;
        width: 100vw;
        height: 100%;
        position: relative;
    }
    .showcase-figure{
        display: block;
        width: 100vw;
        height: 100vh;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    .showcase-image {
        display: block;
        width: 24rem;
        height: 24rem;
        object-fit: cover;
        object-position: center;
        left: calc(50% - 12rem);
        top: calc(50% - 12rem);
        position: relative;
    }
    .title-container{
        width: 100vw;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }
    .title-content{
        display: block;
        left: calc(50% - 14rem);
        top: calc(50% + 4rem);
        position: absolute;
        color: #fff;
        opacity: 0;
    }
    .back-button{
        background: url("~@/../src/assets/icon-close.svg") center center no-repeat;
        background-size: 2rem 2rem;
        position: absolute;
        top: 2rem;
        left: 2rem;
        width: 4rem;
        height: 4rem;
        z-index: 3;
        border-radius: 100%;
        border: 1px rgba(255,255,255,1) solid;
        transform: scale(0,0);
        transition: 0.8s border;
    }
    .back-button:hover{
        border: 1px rgba(255,255,255,0) solid;
    }
    #showcase-canvas{
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100%;
        z-index: -1;
        pointer-events: none;
    }
</style>